import Alert from 'react-s-alert';
const defaultOpts = {
  position: 'bottom-right',
  effect: 'slide',
  beep: false,
  timeout: 3000,
  html: true,
};

export default {
  log(message, opts) {
    return Alert.info(message, Object.assign({}, defaultOpts, opts));
  },
  error(message, opts) {
    return Alert.error(message, Object.assign({}, defaultOpts, opts));
  },
  warning(message, opts) {
    return Alert.warning(message, Object.assign({}, defaultOpts, opts));
  },
  success(message, opts) {
    return Alert.success(message, Object.assign({}, defaultOpts, opts));
  },
  close(id) {
    return Alert.close(id);
  },
  closeAll() {
    return Alert.closeAll();
  },
};


