import React, {Component} from 'react';
import { connect } from 'react-redux';
import navAction from '../actions/nav';
import avatar from '../img/avatar.jpg';

const menu = [
  {
    name: 'Danh sách người dùng',
    items: [
      {
        name: 'Tất cả',
        id: 'list-all'
      },
      {
        name: 'Bác sĩ',
        id: 'list-doctor'
      },
      {
        name: 'Người dùng thường',
        id: 'list-patient'
      },
      {
        name: 'Tài khoản vi phạm',
        id: 'list-violate'
      }
    ]
  },
  {
    name: 'Gửi thông báo',
    items: [
      {
        name: 'Tất cả',
        id: 'send-noti-all'
      },
      {
        name: 'Bác sĩ',
        id: 'send-noti-doctor'
      },
      {
        name: 'Người dùng thường',
        id: 'send-noti-normal-user'
      },
      {
        name: 'Lựa chọn',
        id: 'send-noti-option'
      }
    ]
  },
  {
    name: 'Thống kê',
    items: [
      {
        name: 'Lưu lượng',
        id: 'stat-transport'
      },
      {
        name: 'Thu chi',
        id: 'stat-money'
      },
    ]
  },
  {
    name: 'Báo cáo vi phạm',
    items: [
      {
        name: 'Tất cả',
        id: 'report-all'
      },
      {
        name: 'Đã xử lý',
        id: 'report-handled'
      },
      {
        name: 'Chưa xử lý',
        id: 'report-unhandled'
      },
    ]
  }
]


class SidebarLeft extends Component {

  menuLv2 = (name, id) => (
    <li className="title menu-lvl-2 p-1" key={id}>
      <button className={"text-left " + ((this.props.currentId === id) ? "active" : "")} onClick={() => {
        this.props.navigate(id)
      }}>
        <span className="icon">
          <i className="far fa-circle" />
        </span>
        {name}
      </button>
    </li>
  );

  menuLv1 = (id, name, children) => (
    <li key={id}>
      <div className="clearfix title menu-lvl-1 pt-3 pb-3 pl-1 position-relative" data-toggle="collapse"
        data-target={`#collapse-left-${id}`} aria-expanded={id===0}>
        <span className="icon"><i className="fas fa-users"></i></span>
        <div className="d-inline">
          <span className="text">{name}</span>
          <span className="float-right icon-collapse pr-3"><i className="fas fa-chevron-down"></i></span>
        </div>
      </div>
      <div className={(id == 0) ? "show" : "collapse"} id={`collapse-left-${id}`} data-parent=".menu">
        <ul>
          {children.map((e, i) => this.menuLv2(e.name, e.id))}
        </ul>
      </div>
    </li>
  );

  constructor(props) {
    super(props);
    this.state = {}
  }

  render() {
    return (
      <div className="block-sidebar-left position-absolute">
        <div className="block-user p-2 d-flex clearfix">
          <div className="avatar d-flex align-items-center rounded-circle">
            <img src={avatar} alt="avatar" className="float-left" width="100%" />
          </div>
          <div className="info pl-3 d-flex flex-wrap align-content-around">
            <p className="name w-100">Con gà con</p>
            <p className="status w-100"><span className="pl-1 pr-1"><i className="fas fa-circle"></i></span>Online</p>
          </div>
        </div>
        <form action="#" method="get" className="form-search m-2">
          <div className="input-search position-relative">
            <input type="text" className="p-2 w-100" placeholder="Search..." />
            <span className="icon-search p-2 h-100"><i className="fas fa-search"></i></span>
          </div>
        </form>
        <div className="menu-tree">
          <p className="active p-3">MAIN NAVIGATION</p>
          <ul className="menu">
            {
              menu.map((e, i) => this.menuLv1(i, e.name, e.items))
            }
          </ul>
        </div>
      </div>
    );
  };
}

const mapStateToProps = reducers => ({currentId: reducers.nav.id});
const mapDispatchToProps = dispatch => ({
  navigate: id => dispatch(navAction.changeView(id))
});
export default connect(mapStateToProps, mapDispatchToProps)(SidebarLeft);



