import React from 'react';
import ReactDOM from 'react-dom';
import { createStore } from 'redux';
import { Provider } from 'react-redux';
import reducers from './reducers';
import App from './components/app';
import './css/style.css';
import './css/app.css'
import './css/sidebar-top.css'
import './css/sidebar-left.css'
import './css/sidebar-right.css'
import './css/main-content.css'
import './css/test.css'
import './css/snippet/list-all.css'
import './css/snippet/list-basic.css'
import './css/snippet/list-nguoidungthuong.css'
import './css/snippet/list-vipham.css'
import './css/snippet/report-all.css'
import './css/snippet/report-done.css'
import './css/snippet/report-waiting.css'
import './css/snippet/send-note-all.css'
import './css/snippet/send-note-bacsi.css'
import './css/snippet/send-note-nguoidungthuong.css'
import './css/snippet/send-note-luachon.css'
import './css/snippet/statistical-luuluong.css'
import './css/snippet/statistical-thuchi.css'
import './css/login.css'

//start react s alert
import 'react-s-alert/dist/s-alert-default.css';
// optional - you can choose the effect you want
import 'react-s-alert/dist/s-alert-css-effects/slide.css';
import 'react-s-alert/dist/s-alert-css-effects/scale.css';
import 'react-s-alert/dist/s-alert-css-effects/bouncyflip.css';
import 'react-s-alert/dist/s-alert-css-effects/flip.css';
import 'react-s-alert/dist/s-alert-css-effects/genie.css';
import 'react-s-alert/dist/s-alert-css-effects/jelly.css';
import 'react-s-alert/dist/s-alert-css-effects/stackslide.css';
//end react s alert


import registerServiceWorker from './registerServiceWorker';

const store = createStore(reducers);

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>, document.getElementById('root'));
registerServiceWorker();











