import React, {Component} from 'react';

const ReactHighcharts = require('react-highcharts'); // Expects that Highcharts was loaded in the code.

const configLineTruyCap = {
  title: {
    text: ''
  },

  subtitle: {
    text: 'Source: admin Nguyễn Thành Trung'
  },

  chart: {
    type: 'spline'
  },

  xAxis: {
    type: 'datetime',
    dateTimeLabelFormats: { // don't display the dummy year
      day: '%b %e',
      week: '%b %e'
    },
    title: {
      text: 'Date'
    }
  },
  yAxis: {
    title: {
      text: 'Snow depth (m)'
    },
    min: 0
  },
  tooltip: {
    headerFormat: '<b>{series.name}</b><br>',
    pointFormat: '{point.x:%e. %b}: {point.y:.2f} m'
  },

  plotOptions: {
    series: {
      pointStart: Date.UTC(2018, 0, 1),
      pointInterval: 24 * 3600 * 1000 * 7
    },
    spline: {
      marker: {
        enabled: true
      }
    }
  },

  series: [{
    name: 'Lưu lượng truy cập',
    data: [100, 200, 300, 100, 150, 250, 500, 300]
  }],
  credits: {
    enabled: false
  },

  responsive: {
    rules: [{
      condition: {
        maxWidth: 500
      },
      chartOptions: {
        legend: {
          layout: 'horizontal',
          align: 'center',
          verticalAlign: 'bottom'
        }
      }
    }]
  }
};
const configLineCauHoiMoi = {
  title: {
    text: ''
  },

  subtitle: {
    text: ''
  },

  chart: {
    type: 'spline'
  },

  xAxis: {
    type: 'datetime',
    dateTimeLabelFormats: { // don't display the dummy year
      day: '%b %e',
      week: '%b %e'
    },
    title: {
      text: 'Date'
    }
  },
  yAxis: {
    title: {
      text: 'Snow depth (m)'
    },
    min: 0
  },
  tooltip: {
    headerFormat: '<b>{series.name}</b><br>',
    pointFormat: '{point.x:%e. %b}: {point.y:.2f} m'
  },

  plotOptions: {
    series: {
      pointStart: Date.UTC(2018, 0, 1),
      pointInterval: 24 * 3600 * 1000 * 7
    },
    spline: {
      marker: {
        enabled: true
      }
    }
  },

  series: [{
    name: 'Lưu lượng câu hỏi mới',
    data: [100, 200, 300, 100, 150, 250, 500, 300]
  }],
  credits: {
    enabled: false
  },

  responsive: {
    rules: [{
      condition: {
        maxWidth: 500
      },
      chartOptions: {
        legend: {
          layout: 'horizontal',
          align: 'center',
          verticalAlign: 'bottom'
        }
      }
    }]
  }
};
const configLineDangKyMoi = {
  title: {
    text: ''
  },

  subtitle: {
    text: ''
  },

  chart: {
    type: 'spline'
  },

  xAxis: {
    type: 'datetime',
    dateTimeLabelFormats: { // don't display the dummy year
      day: '%b %e',
      week: '%b %e'
    },
    title: {
      text: 'Date'
    }
  },
  yAxis: {
    title: {
      text: 'Snow depth (m)'
    },
    min: 0
  },
  tooltip: {
    headerFormat: '<b>{series.name}</b><br>',
    pointFormat: '{point.x:%e. %b}: {point.y:.2f} m'
  },

  plotOptions: {
    series: {
      pointStart: Date.UTC(2018, 0, 1),
      pointInterval: 24 * 3600 * 1000 * 7
    },
    spline: {
      marker: {
        enabled: true
      }
    }
  },

  series: [{
    name: 'Lưu lượng đăng ký mới',
    data: [100, 200, 300, 100, 150, 250, 500, 300]
  }],
  credits: {
    enabled: false
  },

  responsive: {
    rules: [{
      condition: {
        maxWidth: 500
      },
      chartOptions: {
        legend: {
          layout: 'horizontal',
          align: 'center',
          verticalAlign: 'bottom'
        }
      }
    }]
  }
};

export default class statLuuluong extends Component {

  constructor(props) {
    super(props);
    this.state = {}
  }

  render() {
    return (

      <div className="stat-luuluong">

        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title clearfix" data-toggle="collapse" data-target="#chartLuuluong1">
              <span className="float-left">Lưu lượng truy cập</span>
            </div>
            <div id="chartLuuluong1" data-parent=".stat-luuluong" className="show">
              <ReactHighcharts config={configLineTruyCap}></ReactHighcharts>
            </div>
          </div>
        </div>

        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title clearfix" data-toggle="collapse" data-target="#chartLuuluong2">
              <span className="float-left">Lưu lượng câu hỏi mới</span>
            </div>
            <div id="chartLuuluong2" data-parent=".stat-luuluong" className="collapse">
              <ReactHighcharts config={configLineCauHoiMoi}></ReactHighcharts>
            </div>
          </div>
        </div>

        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title clearfix" data-toggle="collapse" data-target="#chartLuuluong3">
              <span className="float-left">Lưu lượng đăng ký mới</span>
            </div>
            <div id="chartLuuluong3" data-parent=".stat-luuluong" className="collapse">
              <ReactHighcharts config={configLineDangKyMoi}></ReactHighcharts>
            </div>
          </div>
        </div>

      </div>
    );
  };

}


