import React, {Component} from 'react';
import avatar from '../img/avatar.jpg';

export default class SidebarTop extends Component {

  constructor(props) {
    super(props);
    this.state = {}
  }

  render() {
    return (
      <div className="block-sidebar-top clearfix d-flex justify-content-between">

        <div className="content-left d-flex">
          <a href="/" className="logo text-center"><b>Admin</b> Page</a>
          <span className="icon-menu pr-4 pl-4"><i className="fas fa-bars"></i></span>
        </div>

        <div className="content-right d-flex w-100 justify-content-end">
          <span className="icon-mail pr-4 pl-4"><i className="far fa-envelope"></i><sup>1</sup></span>
          <span className="icon-bell pr-4 pl-4"><i className="far fa-bell"></i><sup>2</sup></span>
          <span className="icon-flag pr-4 pl-4"><i className="far fa-flag"></i><sup>3</sup></span>
          <div className="user pr-4 pl-4 d-flex align-items-center position-relative" data-toggle="dropdown">
            <span className="avatar p-0 rounded-circle mr-2">
            </span>
            <span className="name">Con gà con</span>
            <div className="dropdown-menu border-light p-0">
              <div className="user-header text-white p-3">
                <div className="avatar m-auto rounded-circle d-flex align-items-center">
                  <img src={avatar} alt="avatar" width="100%" />
                </div>
                <p className="user-name mt-3 mb-3 text-center">Con gà gáy ò ó o... o
                  <small className="mt-2 mb-2 d-block">Member a ă â b c d đ</small>
                </p>
              </div>
              <div className="user-footer d-flex justify-content-between bg-light">
                <button className="p-2 m-2 border bg-hover-gainsboro">Khác</button>
                <button className="p-2 m-2 border bg-hover-gainsboro">Đổi mật khẩu</button>
                <button className="p-2 m-2 border bg-hover-gainsboro">Đăng xuất</button>
              </div>
            </div>
          </div>
          <span className="icon-gears pr-4 pl-4"><i className="fas fa-cogs"></i></span>
        </div>
      </div>
    );
  };

}


