import  {LOGIN_SUCESS} from "../actions/login";

const initState = {
  isLogin: false,
};

export default (state = initState, action) => {
  switch(action.type) {
    case "@redux/INIT":
      return initState;
    case LOGIN_SUCESS:
      return Object.assign({}, state, {isLogin: true});
    default:
      return state;
  }
}



