import React, {Component} from 'react';

const ReactHighcharts = require('react-highcharts'); // Expects that Highcharts was loaded in the code.

const configPie = {
  chart: {
    plotBackgroundColor: null,
    plotBorderWidth: null,
    plotShadow: false,
    type: 'pie'
  },
  title: {
    text: ''
  },
  tooltip: {
    pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
  },
  plotOptions: {
    pie: {
      allowPointSelect: true,
      cursor: 'pointer',
      dataLabels: {
        enabled: true,
        format: '<b>{point.name}</b>: {point.percentage:.1f} %',
      }
    }
  },

  credits: {
    enabled: false
  },

  series: [{
    innerSize: '50%',
    dataLabels: {
      enabled: true,
      format: '{y}',
      style: {
        fontSize: '14px',
      },
      verticalAlign: 'right',
    },
    name: 'Rate',
    colorByPoint: true,
    data: [{
      name: 'Thu',
      y: 200,
      color: '#3F8CBB',
    }, {
      name: 'Chi',
      y: 150,
      color: '#fab300',
    }]
  }]
}

const configLine = {
  title: {
    text: ''
  },

  subtitle: {
    text: 'Source: admin Nguyễn Thành Trung'
  },

  chart: {
    type: 'spline'
  },

  xAxis: {
    type: 'datetime',
    dateTimeLabelFormats: { // don't display the dummy year
      day: '%b %e',
      week: '%b %e'
    },
    title: {
      text: 'Date'
    }
  },
  yAxis: {
    title: {
      text: 'Snow depth (m)'
    },
    min: 0
  },
  tooltip: {
    headerFormat: '<b>{series.name}</b><br>',
    pointFormat: '{point.x:%e. %b}: {point.y:.2f} m'
  },

  plotOptions: {
    series: {
      pointStart: Date.UTC(2018, 0, 1),
      pointInterval: 24 * 3600 * 1000 * 7
    },
    spline: {
      marker: {
        enabled: true
      }
    }
  },

  colors: ['#6CF', '#39F', '#06C', '#036', '#000'],


  series: [{
    name: 'Thu',
    color: '#3F8CBB',
    data: [100, 200, 300, 100, 150, 250, 500, 300]
  }, {
    name: 'Chi',
    color: '#fab300',
    data: [150, 300, 250, 100, 100, 50, 200, 100]
  },

  ],
  credits: {
    enabled: false
  },

  responsive: {
    rules: [{
      condition: {
        maxWidth: 500
      },
      chartOptions: {
        legend: {
          layout: 'horizontal',
          align: 'center',
          verticalAlign: 'bottom'
        }
      }
    }]
  }
};

export default class statThuchi extends Component {

  constructor(props) {
    super(props);
    this.state = {}
  }

  render() {
    return (
      <div className="stat-thuchi">

        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title clearfix" data-toggle="collapse" data-target="#chartThuchi1">
              <span className="float-left">Lưu lượng thu chi</span>
            </div>
            <div id="chartThuchi1" data-parent=".stat-thuchi" className="show">
              <ReactHighcharts config={configLine}></ReactHighcharts>
            </div>
          </div>
        </div>

        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title clearfix" data-toggle="collapse" data-target="#chartThuchi2">
              <span className="float-left">Tổng thu - tổng chi = 50k</span>
            </div>
            <div id="chartThuchi2" data-parent=".stat-thuchi" className="collapse">
              <ReactHighcharts config={configPie}></ReactHighcharts>
            </div>
          </div>
        </div>

      </div>

    );
  };

}


