import React, {Component} from 'react';
import RichTextEditor from 'react-rte';
import {connect} from 'react-redux';

class SendNoteBacsi extends Component {

  constructor(props) {
    super(props);
    this.state = {
      active_choose_user_to_send: false,
      value: RichTextEditor.createEmptyValue(),
      sortByType: {
        sortBy: 0,    //0 => by id, 1: name, 2: status Lock, 3: by status Ticked //column of table
        sortIdUp: true,
        sortNameAToZ: true,
        sortStatusTick: true,
      },
      userList: this.props.userList,
    };
  }

  onChange = (value) => {
    this.setState({value});
  };

  toggle_choose_user = () => {
    this.setState({
      active_choose_user_to_send: !this.state.active_choose_user_to_send,
    })
  }

  setSortById() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 0;
    sortBy.sortIdUp = !sortBy.sortIdUp;
    this.setState ({
      sortByType: this.state.sortByType,
    })
  }
  setSortByName() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 1;
    sortBy.sortNameAToZ = !sortBy.sortNameAToZ;
    this.setState ({
      sortByType: this.state.sortByType,
    })
  }

  setSortByTick() {
    const sortBy = this.state.sortByType;
    sortBy.sortBy = 3;
    sortBy.sortStatusTick = !sortBy.sortStatusTick;
    this.setState ({
      sortByType: this.state.sortByType,
    })
  }

  toggle_tick_account(i) {
    const user = this.state.userList[i];
    user.tick = !user.tick;
    this.setState({
      userList: this.state.userList,
    });
  };


  render() {

    this.state.userList = this.state.userList.filter((elm)=>elm.type == "doctor");

    return (
      <div className="send-note-bacsi">
        <div className="container-fluid pt-3 pb-3">
          <div className="block-content">
            <div className="block-content-title clearfix">
              <span>
                Gửi mail cho bác sĩ
              </span>
            </div>
            <div className="block-content-body">
              <form action="">
                <div className="mail-title">
                  <label className="d-block p-2">
                    Tiêu đề<span className="text-danger"> * </span>
                    <span className="text-required text-danger text-hide">This field cannot be empty</span>
                  </label>
                  <input type="text" className="text-dark p-2 w-100" />
                </div>
                <div className="mail-content">
                  <label className="d-block p-2">
                    Nội dung<span className="text-danger"> * </span>
                    <span className="text-required text-danger text-hide">This field cannot be empty</span>
                  </label>
                  <RichTextEditor value={this.state.value}
                    onChange={this.onChange} />
                </div>
                <div className="mail-file">
                  <input type="file" className="form-control-file" name="choose-file" />
                </div>
                <div className="user-received d-flex p-2 position-relative">
                  <span>Gửi cho: </span>
                  <span className="content-user-send ml-3 border d-inline-block position-relative">
                    <div className={"d-flex " + (this.state.active_choose_user_to_send ? "active" : "")}
                      onClick={this.toggle_choose_user} data-toggle="collapse" data-target=".target-content-user">
                      <span className="active text-center text-capitalize d-inline-block">tất cả</span>
                      <span className="text-center text-capitalize d-inline-block">được lựa chọn</span>
                    </div>
                  </span>
                  <span className="position-absolute target-content-user collapse">
                    <input type="text" name="search" placeholder="Search.." className="p-0 pl-2 m-0" />
                  </span>
                </div>
                <div className="mini-block-content collapse target-content-user">

                  <table className="table table-hover">
                    <thead className="thead-dark">
                    <tr className="">
                      <th onClick={() => this.setSortById()}>Id
                        <span className="float-right">
                        <i className={"fas " + ((this.state.sortByType.sortIdUp) ? "fa-sort-down" : "fa-sort-up")}></i>
                      </span>
                      </th>
                      <th onClick={() => this.setSortByName()}>Họ và tên
                        <span className="float-right">
                        <i className={"fas " + ((this.state.sortByType.sortNameAToZ) ? "fa-sort-down" : "fa-sort-up")}></i>
                      </span>
                      </th>
                      <th>Chi tiết</th>
                      <th className="text-center" onClick={() => this.setSortByTick()}>Đánh dấu
                        <span className="float-right">
                        <i className={"fas " + ((this.state.sortByType.sortStatusTick) ? "fa-sort-down" : "fa-sort-up")}></i>
                      </span>
                      </th>
                    </tr>
                    </thead>
                    <tbody>
                    {
                      this.state.userList.map((elm, i) => (
                        <tr key={i} onClick={() => this.toggle_tick_account(i)}>
                          <td>{elm.id}</td>
                          <td>{elm.name}</td>
                          <td>{elm.detail}</td>
                          <td className="text-center">
                            <span><i className={((elm.tick)?"fas fa-check":"text-secondary far fa-square")}></i></span>
                          </td>
                        </tr>
                      ))
                    }
                    </tbody>
                  </table>

                </div>
                <div className="mail-button">
                  <button className="p-2 mr-2 mt-2 border bg-hover-gainsboro" type="submit">Gửi</button>
                  <button className="p-2 mr-2 mt-2 border bg-hover-gainsboro">Xóa</button>

                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  };

}

const mapStateToProps = state => ({userList: state.user.userList});

export default connect(mapStateToProps)(SendNoteBacsi);



